import GeneticAlgorithm from '../services/GeneticAlgorithm';
import NapZak from '../services/NapZak';

import { GA_EVOLVE, SETUP_GA } from '../actions'

function reducer(state, action) {
  switch (action.type) {
    case GA_EVOLVE:
      return Object.assign({}, state, {
        population: state.ga.evolve(state.population),
        generation: state.generation + 1
      })

    case SETUP_GA:
      const config = action.config
      return Object.assign({}, state,
        createGaState(config))

    default:
      return state
  }
}

function createGaState(config) {
  const napzak = new NapZak(config.numberOfItems)
  napzak.weightCapacity = Math.floor(
    napzak.totalWeight() * 0.5)
  const ga = new GeneticAlgorithm(napzak, config)

  return {
    population: ga.createPopulation(),
    generation: 1,
    ga: ga
  }
}

export default reducer
export { createGaState }




